import { Link } from "react-router-dom";
import styles from "./styles.module.css"

export default function Footer({ curPage }: FooterProps)
{
	return (
		<div className={styles.root}>
			{curPage != "map" && <Link to={"/"}></Link>}
			{curPage != "timetable" && <Link to={"/timetable"}></Link>}
			{curPage != "quest" && <Link to={"/quest"}></Link>}
			{curPage != "store" && <Link to={"/store"}></Link>}
			{/* {curPage != "race" && <Link to={"/race"}></Link>} */}
		</div>
	);
}

interface FooterProps
{
	curPage?: "map" | "timetable" | "quest" | "store" | "race"
}
